import { promiseFactoryToObservable } from '@sourcegraph/observable'
import { type readFileToolReg } from './read_file.common'
import { readFileContentWith } from './read_file.util'

export const nodeReadFileTool: NonNullable<typeof readFileToolReg.fn> = (
	{ args },
	{ dir, dirs, threadID, filesystem },
) => {
	const readFileContentFn = readFileContentWith({ readFileFn: filesystem.readFile })
	return promiseFactoryToObservable(async (signal) =>
		readFileContentFn({ args }, { dir, dirs, threadID, filesystem }, signal),
	)
}
